<template>
    <ul class="sub_nav">
        <li>
            <router-link to="/list"
                :class="currentid==''?'active':''"
                 @click.native="refresh"
                 tag="a"
            >
                全部
            </router-link>
            <router-link v-for="(item) in categories" :key="item.id"
                :to="`/list/${item.id}`" 
                :class="item.id==currentid?'active':''"
                @click="link(item.id)"
                tag="a"
                >
                {{item.name}}
            </router-link>

            
        </li>
    </ul>
</template>
<script>
export default {
  name: 'innerleftmenu',
  props:['currentid'],
  data(){
      return{
          //从路由中接收params值
          currentCategoryId:this.$route.params.id
      }
  },
  computed:{
      categories(){
          return this.$store.state.category.category
      }
  },
  mounted:function(){
      //state里没有category 则触发store里的action 向后台请求数据
      this.$store.dispatch('asyncGetdata');
  },
  methods:{
      //点击当前路由实现跳转
　　　　link(id){
            this.$route.push(`/list/${id}`);
        }
　　}
}
</script>

