<template>
 <div class="classify_content" ref="list" id="test">
    <div class="wrap">
        <header class="content_header clearfix">
            <h2 v-if="params.categoryId == null">全部</h2>
            <h2 v-else>{{currentCategory.name}}</h2>
        </header>
        <ul class="content_list">
            <li v-for="cookbook in cookbooks" :key='cookbook.id'>
                <div class="list_img">
                    <router-link :to="`/view/${cookbook.id}`" tag="a">
                    <!-- <a :href="`/view/${cookbook.id}`"> -->
                    <img :src="`/public/uploads/cookbooks/${cookbook.cover}`" alt="" />
                    <!-- </a> -->
                    </router-link>
                </div>
                <div class="content_txt">
                    <h3>
                        <router-link :to="`/view/${cookbook.id}`" tag="a">
                        <!-- <a :href="`/view/${cookbook.id}`"> -->
                            {{cookbook.name}}
                        <!-- </a> -->
                        </router-link>
                    </h3>
                    <span>
                         <router-link :to="`/home/space-recipe/${cookbook.userId}`" tag="a">
                        <!-- <a :href="`/home/space-recipe/${cookbook.userId}`"> -->
                        {{cookbook.username}}
                         </router-link>
                    </span>
                    <p  class="raw_material">
                        原料：
                        <i v-for="m in cookbook.ingredients.m" :key="m.id">
                            {{m.k}}
                        </i>
                        <i v-for="s in cookbook.ingredients.s" :key="s.id">
                            {{s.k}}
                        </i>
                    </p>
                    <p class="describe">
                        {{cookbook.description}}
                    </p>
                    <p class="comment">
                        <span class="love">{{cookbook.favoriteCount}}</span>
                        <span class="msg">{{cookbook.commentCount}}</span>
                    </p>
                </div>
            </li>
        </ul>
        <div class="no-text" v-if="tipsBoolean"  v-html='tipsTxt'></div>
    </div>
</div>
</template>
<script>
import { getListApi } from '@/api/index';
export default {
    data(){
        return{
            cookbooks:[],
            params:{
               categoryId:this.$route.params.id,
               page:1
            },
            currentCategory:{},
            isLoading:false,
            tipsBoolean:false,
            tipsTxt:''
        }
    },
    mounted:function(){
        this.getList();    
        window.addEventListener('scroll',this.handleScroll,true);
    },
    destroyed() {
        window.removeEventListener('scroll', this.handleScroll);
    },
    methods:{
        async getList(){
            this.isLoading = true;
            let res = await getListApi(this.params);
            if(res && !res.data.code){
                if(res.data.currentCategory){
                    this.currentCategory = res.data.currentCategory;
                    //使用v-model绑定数据 接收子组件向父组件传值 语法糖
                    this.$emit('input',res.data.currentCategory)
                };
                if(res.data.cookbooks.length == 0){
                    this.tipsTxt = '暂无最新数据';
                    this.tipsBoolean = true;
                    // this.isLoading = true;
                    return;
                }else{
                    this.cookbooks = this.cookbooks.concat(res.data.cookbooks);
                    this.isLoading = false;
                }
                
                
                
                
            }
        },
        handleScroll(){
            //变量scrollTop是滚动条滚动时，距离顶部的距离
            var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
            //变量windowHeight是可视区的高度
            var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
            //变量scrollHeight是滚动条的总高度
            var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;

            if(this.isLoading) return;
            
            if((scrollTop + windowHeight) - scrollHeight >= -1 ){
                this.getList(this.params);
                this.params.page += 1;
                
            }
        }
        
        
    }
}
</script>



